
<template>
  <div>

    <div class="form">
      task: <input type="text" name="" id="" v-model="task" @keyup="input">
      <p>{{task.length}}/20</p>
      <p v-if="error"></p>
    </div>
    <ul>
      <!-- <app-list v-for="item in task_list" :item="item">{{item}}</app-list> -->
      <li v-for="item in task_list" :item="item">{{item}}</li>
    </ul>
    <div v-if="task">
        <a class="btn btn-default" @click="add"></a>
    </div>
    <div v-else="task">
        <p class="test-danger">error</p>
    </div>

  </div>
</template>

<script>


export default {
  name: 'app',
  data () {
    return {
      task: "sample task",
      task_list: [
        "d",
        "f"
      ],
      error:false
    }
  },
  methods: {
    add: function(){
      this.task_list.push(this.task)
    },
    input: function(){
      if(this.task.length > 20){
        this.error = true;
      }else {
        this.error = false;
      }
    }
  },
}

</script>

<style scoped>
.form {
  display: block;
  padding: 32px;
}
li {
display: block;
text-align: center;
margin: 0 0 8px;
}
input {
padding: 4px 2px;
border: solid 1px #777777;
}
</style>



