import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

import App from './App.vue'
import PageAbout from './PageAbout.vue'
import PageForm from './PageForm.vue'
import PageBirthday from './PageBirthday.vue'
//import PageTodo from './PageTodo.vue'
// import appList from './appList.vue'

Vue.component("app-about", PageAbout)
Vue.component("app-form", PageForm)
Vue.component("app-birthday", PageBirthday)
  // Vue.component("app-birthday",PageTodo)

const routes = [{
    path: '/about',
    component: PageAbout
  }, {
    path: '/form',
    component: PageForm
  }, {
    path: '/birthday',
    component: PageBirthday
  }
  //{ path: '/todo', component: PageTodo }
]

const router = new VueRouter({
  routes // routes: routes 
})

new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



