
<template>
<div>

    <h2 style="display: none" id="result"></h2>
    <form>
        <div class="room">
            <input type="number" id="year" name="year" value=2000>
            <input type="number" id="month" name="month" value=1 min=1 max=12>
            <input type="number" id="day" name="day" value=1 min=1 max=31>
        </div>
        <a class="btn" value="" @click="add"></a>
    </form>

</div>
</template>

<script>
export default {

    methods: {
        add: function() {
            //return false;
            calculateBirthday();
        }
    },
}

function getBirthday() {
    var year = document.getElementById("year").value;
    var month = document.getElementById("month").value;
    var day = document.getElementById("day").value;

    return new Date(year, month - 1, day);
}

function getDateDiff(previous, latest) {
    var diff = latest.getTime() - previous.getTime();

    var days = Math.floor(diff / (1000 * 60 * 60 * 24));
    diff -= days * (1000 * 60 * 60 * 24);
    var hours = Math.floor(diff / (1000 * 60 * 60));
    diff -= hours * (1000 * 60 * 60);
    var mins = Math.floor(diff / (1000 * 60));
    diff -= mins * (1000 * 60);
    var seconds = Math.floor(diff / (1000));
    diff -= seconds * (1000);

    return {
        days: days,
        hours: hours,
        mins: mins,
        seconds: seconds
    };
}

function calculateBirthday() {
    var now = new Date();
    var birth = getBirthday();

    var diff = getDateDiff(birth, now);
    var message = "" + diff.days + "";

    var result = document.getElementById("result");
    result.textContent = message;
    result.style.display = "";
}
</script>

<style scoped>
form {
    display: block;
    padding: 32px;
}

.btn {
    display: inline-block;
    padding: 8px 16px;
    margin: 32px auto;
    border: solid 1px #777777;
    border-radius: 8px;
}
.room {
display:block;
white-space: nowrap;
}
input {
padding: 4px 2px;
border: solid 1px #777777;
}
</style>



